import { LitElement, html, customElement, property, css } from "lit-element";

import { _calculateDateStringFromDate, _convertTimeStringToDate, _convertDateToTimeString, _convertDecimalToClock } from "../helpers";
// Interfaces
import { StateInterface } from "../interfaces/StateInterface";
import { DayInterface } from "../interfaces/DayInterface";
import { DefaultTimesInterface } from "../interfaces/DefaultTimesInterface";
import { SavedDataInterface } from "../interfaces/SavedData";
import { Requester } from "../requester";
const STORAGE_KEY = '__zeitkontrast__'

@customElement('day-view')
class DayView extends Requester(LitElement) {
    @property ({ type: Object}) state: StateInterface

    constructor(){
        super();
    }
    
    connectedCallback() {
        super.connectedCallback();
        console.log('day view connected');
    }

    /* firstUpdated(){

    } */

    updated(changedProperties:Map<string, any>){
        if (changedProperties.has("state")){
            if (!this.state?.currentDayData){
                this.loadDay(_calculateDateStringFromDate(new Date()));
            }
        }
    }

    static get styles() {
        return css`
                :host {
                    font-family: Helvetica, "Helvetica Neue", Arial, sans-serif;
                    display:flex;
                    flex-direction: column;
                    align-items: center;
                    margin-top: 50px;
                }
                .container{
                    box-sizing: border-box;
                    margin-left: 20px;
                    margin-right: 20px;
                    padding-right: 8px;
                    padding-left: 8px;
                    max-width: 100%;
                }
                label {
                    color: #9e9e9e;
                    position: relative;
                    top: 0;
                    left: 0;
                    font-size: 1rem;
                    cursor: text;
                    -webkit-transition: color .2s ease-out, -webkit-transform .2s ease-out;
                    transition: color .2s ease-out, -webkit-transform .2s ease-out;
                    transition: transform .2s ease-out, color .2s ease-out;
                    transition: transform .2s ease-out, color .2s ease-out, -webkit-transform .2s ease-out;
                    -webkit-transform-origin: 0% 100%;
                    transform-origin: 0% 100%;
                    text-align: initial;
                    -webkit-transform: translateY(12px);
                    transform: translateY(12px);
                }
                input {
                    background-color: transparent;
                    border: none;
                    border-bottom: 1px solid #9e9e9e;
                    border-radius: 0;
                    outline: none;
                    height: 3rem;
                    width: 100%;
                    font-size: 16px;
                    margin: 0 0 8px 0;
                    padding: 0;
                    -webkit-box-shadow: none;
                    box-shadow: none;
                    -webkit-box-sizing: content-box;
                    box-sizing: content-box;
                    -webkit-transition: border .3s, -webkit-box-shadow .3s;
                    transition: border .3s, -webkit-box-shadow .3s;
                    transition: box-shadow .3s, border .3s;
                    transition: box-shadow .3s, border .3s, -webkit-box-shadow .3s;
                }
                .pickers-container{
                    display: flex;
                    flex-direction: column;
                }
                .picker {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                }
                .break-picker-container{
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                }
                .break-container{
                    display:flex;
                    flex-direction: row;
                    align-items: center;
                    font-size: 1.5rem;
                }
                .work-result{
                    font-size: 2rem;
                }
                .worked-hours{
                    text-decoration: underline;
                }
                h2 {
                    margin-top: 70px;
                }
                mwc-button {
                --mdc-theme-primary: #D0021B;
                --mdc-theme-on-primary: white;
                }
        `
    }

    render(){
        let decimalNetto;
        let decimalBrutto;

        if (this.state?.currentDayData){
            decimalNetto = this._calculateNettoDecimal(this.state.currentDayData.startTime, this.state.currentDayData.endTime, this.state.currentDayData.breaks);
            decimalBrutto = this._calculateBruttoDecimal(this.state.currentDayData.startTime, this.state.currentDayData.endTime);
        }

        return html`
        <main class="container">
            <label for="date">date</label>
            <app-datepicker
                id="date"
                .label=${"date"}
                .min=${"1970-01-01"}
                @datepicker-value-updated=${(e:CustomEvent) => this._changeDate(e)}
                .value=${_calculateDateStringFromDate(this.state?.currentDayData?.startTime)}
            ></app-datepicker>
            <div class="pickers-container">
                <div class="picker">
                    <label for="time-start">start time</label>
                    <input 
                        @change=${(e:Event) => this._changeStartTime(e)}
                        .value=${_convertDateToTimeString(this.state?.currentDayData?.startTime)}
                        id="time-start" 
                        type="time" 
                        required>
                </div>
                
                <div class="picker">
                    <label for="time-end">end time</label>
                    <input 
                        @change=${(e:Event) => this._changeEndTime(e)}
                        .value=${_convertDateToTimeString(this.state?.currentDayData?.endTime)}
                        id="time-end" 
                        type="time" 
                        required>
                </div>
            </div>

            <div class="break-picker-container">
                <mwc-icon-button
                icon="add"
                @click=${this._addBreak}
                ></mwc-icon-button>
                <div class="picker">
                    <label for="break">add break</label>
                    <input 
                        .value=${"00:15"}
                        id="break" 
                        type="time" 
                        required>
                </div>
            </div>
            ${this.renderWorkedHours(decimalNetto)}
            ${this._renderBreaks(this.state?.currentDayData?.breaks)}
            </main>
            <div>
                <mwc-button 
                    .disabled=${this.state?.saveDisabled}
                    raised
                    @click=${() => this.saveDay()} 
                    label="Save Day" 
                    icon="save">
                </mwc-button>
                <mwc-button 
                    .disabled=${!this.state?.currentDayData?.exists}
                    raised
                    @click=${() => this.deleteDay()} 
                    label="Delete Day" 
                    icon="delete">
                </mwc-button>
            </div>
        `
    }

    private renderWorkedHours(decimalNetto: number){
        if (decimalNetto){
            return html`
            <p class="work-result">You worked for <span class="worked-hours">${_convertDecimalToClock(decimalNetto)} hours</span>. (${decimalNetto.toFixed(2)} decimal)</p>
            `
        }
    }

    private _renderBreaks(breaks:string[]){
        if (breaks){
            return html`
            ${breaks.length>0?html`
            <h2>breaks</h2>
            `:html``}
           ${breaks.map((_break, index) => html`
            <div class="break-container">
            <p>break ${index+1}: ${_break}</p>
            <mwc-icon-button icon="delete" @click=${() => this.deleteBreak(index)}></mwc-icon-button>
            </div>
            `)}
        `
        }
    }

    private _calculateBruttoDecimal(startTime:Date, endTime: Date){
        let hours = Math.abs(endTime.getTime() - startTime.getTime()) / 36e5;
        return hours;
    }

    private _calculateNettoDecimal(startTime:Date, endTime: Date, breaks: string[]){
        let endTimeClone = new Date(endTime);
        breaks.forEach(_break => {
            this._substractTimeStringFromDate(endTimeClone, _break);
        })
        let hours: number;
        if (endTimeClone<startTime){
            hours = Math.abs(startTime.getTime() - endTimeClone.getTime()) / 36e5;
        }
        else {
            hours = Math.abs(endTimeClone.getTime() - startTime.getTime()) / 36e5;
        }
        return hours;
    }

    private _substractTimeStringFromDate(date: Date, time:string){
        date.setHours(date.getHours() - parseInt(time.split(":")[0]));
        date.setMinutes(date.getMinutes() - parseInt(time.split(":")[1]));
    }

    private _changeStartTime(e:Event){
        const input = e.target as HTMLInputElement;
        const startTimeString = input.value;
        const startTime = _convertTimeStringToDate(startTimeString, this.state.currentDayData.startTime);
        const endTime = new Date(this.state.currentDayData.endTime);
        if (endTime<startTime){
            endTime.setDate(endTime.getDate()+1)
        }
        this.setTime(startTime, endTime);
    }

    private _changeEndTime(e:Event){
        const input = e.target as HTMLInputElement;
        const endTimeString = input.value;
        const endTime = _convertTimeStringToDate(endTimeString, this.state.currentDayData.startTime);
        if (endTime < this.state.currentDayData.startTime){
            endTime.setDate(endTime.getDate() + 1);
        }
        this.setTime(this.state.currentDayData.startTime, endTime);
    }

    private _changeDate(e:CustomEvent){
        //like 2020-07-19
        const inputDate = e.detail.value; 
        this.loadDay(inputDate);
    }

    private _addBreak(){
        const input = this.shadowRoot.querySelector('#break') as HTMLInputElement; 
        if (input.value !== "00:00"){
            this.addBreak(input.value);
        }
    }
}